<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" >
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>


		<!--列表-->
		<el-table :data="departments" border highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
			<el-table-column label="部门信息" width="560" align="center">
				<el-table-column prop="name" label="名称" width="140" column-key="id" sortable align="center">
				</el-table-column>
				<el-table-column prop="description" label="部门职能" width="300" sortable align="center">
				</el-table-column>
				<el-table-column prop="responsorName" label="负责人" width="120" sortable align="center">
				</el-table-column>
			</el-table-column>

			<el-table-column label="系统属性" width="320" align="center">
				<el-table-column prop="creatorName" label="创建者" width="120" sortable align="center">
				</el-table-column>
				<el-table-column type="date" prop="createTime" label="创建时间" min-width="180" sortable align="center">
				</el-table-column>
				<el-table-column prop="updatorName" label="最后更新人" min-width="180" sortable align="center">
				</el-table-column>
				<el-table-column type="date" prop="lastmodifiedTime" label="最后更新时间" min-width="180" sortable align="center">
				</el-table-column>
			</el-table-column>

			<el-table-column label="操作" width="150" fixed="right">
				<template scope="scope">
					<el-button size="small"  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination
					class="pagenation"
					@size-change="handleSizeChange"
					@current-change="handleCurrentChange"
					:current-page="pagination.currentPage"
					:page-sizes="pagination.pageSizes"
					:page-size="pagination.pageSize"
					layout="total, sizes, prev, pager, next, jumper"
					:total="pagination.total">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="120px"  :rules="editFormRules" ref="editForm">
				<el-form-item label="部门名称" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="部门负责人" prop="sort">
					<el-select v-model="editForm.responsorName" placeholder="请选择部门负责人">
						<el-option
								v-for="item in departmentUsers"
								:label="item.name"
								:value="item.name">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="部门职能描述">
					<el-input type="textarea" v-model="editForm.description"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="120px" :rules="editFormRules" ref="addForm">
				<el-form-item label="部门名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="部门职能描述">
					<el-input type="textarea" v-model="addForm.description"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
    import { requestApi, Enum, msgUtils } from '../../api/api';

	export default {
		data() {
			return {
				departments: [],
                pagination: {
                    total: 0,
                    currentPage: 1,
                    pageSizes: [10, 20, 50, 100],
                    pageSize: 10
                },
				listLoading: false,
				sels: [],//列表选中列
				departmentUsers: [], // 属于某部门的所有人员的信息
				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
                    name: [
                        {required: true, message: '请输入部门名称'}
                    ],
                    description: [
                        {required: true, message: '请输入部门职能'}
                    ]
				},
				//编辑界面数据
            	//name sort spec unit number price
				editForm: {
                    "description":'',
                    "name":'',
                    "responsorId":'',
                    "responsorName":''
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				//新增界面数据
                addForm: {
                    "description":'',
                    "name":''
                }

			}
		},
		methods: {
			handleCurrentChange(val) {
                this.pagination.currentPage = val;
				this.getDepartments();
			},
            handleSizeChange(val) {
                this.pagination.pageSize = val;
                this.getDepartments();
			},
			//获取部门列表
			getDepartments() {
				this.listLoading = true;
                requestApi.department.findAll(this, this.pagination.currentPage-1, this.pagination.pageSize).then(res => {
                    this.listLoading = false;
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code){
                        this.pagination.total = res.data.totalElements;
                        this.departments = res.data.content;
                    }
                }, err => {
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					requestApi.department.delete(this, para).then(res => {
						//NProgress.done();
                        this.listLoading = false;
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code) {
                            this.getDepartments();
                        }else {
                            msgUtils.warning(this, res.msg);
                        }
					}, error => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
					});
				})
			},
			//显示编辑界面
			handleEdit: function (index, row) {
                // 这个方法必须传formData
                let formData  = new FormData();
                formData .append('departmentId', row.id);
				requestApi.user.findByDepartment(this, formData).then(res => {
                    res = res.body;
                    console.log(res);
                    if(res.code === Enum.SUCCESS.code) {
                        this.departmentUsers = res.data;
                        this.editFormVisible = true;
                    }else {
                        msgUtils.warning(this, res.msg);
                    }
				}, error=>{
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
				});
				this.editForm = Object.assign({}, row);
				this.editFormVisible = true;
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
                    "description":'',
                    "name":''
                };
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// 先根据 responsorName 找出 responsorId
                            this.departmentUsers.forEach(departmentUser => {
                                if(departmentUser.name ===  para.responsorName){
                                    para.responsorId = departmentUser.id;
                                }
                            });
                            requestApi.department.update(this,para).then(res => {
                                this.editLoading = false;
                                res = res.body;
                                if(res.code === Enum.SUCCESS.code) {
                                    this.getDepartments();
                                    this.editFormVisible = false;
                                }else {
                                    msgUtils.warning(this, res.msg);
                                }
                            },error => {
                                this.editLoading = false;
                                msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                            });
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							requestApi.department.add(this, para).then(res => {
								this.addLoading = false;
								//NProgress.done();
                                res = res.body;
                                if(res.code === Enum.SUCCESS.code) {
                                    this.$refs['addForm'].resetFields();
                                    this.getDepartments();
                                    this.addFormVisible = false;
                                }else {
                                    msgUtils.warning(this, res.msg);
                                }
							}, error => {
                                this.addLoading = false;
                                msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
			    var els = [];
			    this.sels.forEach(item => {
			        var el = {};
			        el.id = item.id;
                    els.push(el);
				});
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					requestApi.department.deletes(this, els).then(res => {
                        this.listLoading = false;
                        //NProgress.done();
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code) {
                            this.getDepartments();
                        }else {
                            msgUtils.warning(this, res.msg);
                        }
                    }, error => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
				})
			}
		},
		mounted() {
			this.getDepartments();
		}
	}

</script>

<style scoped>
	.pagenation {
		float: right;
	}
</style>


